.face-bio {
    padding: 1rem;
    justify-content: center;
    color: black;
    height:auto;
    width:350px;
    background: rgb(143,90,233,0.3);
    animation: bounce; /* referring directly to the animation's @keyframe declaration */
  animation-duration: 4s;
}

.face-bio__text {
    font-size: 1.5rem;
    font-weight: 400;
}

.face-bio__text--primary {
    font-weight: bolder;
    
}

.bio-box {
    margin-bottom: 1rem;
    padding: 1rem;
   
}